import React, { useState } from "react"
import styled from "styled-components"

import RichImage from "./richImage"

const Slideshower = styled.ul`
  overflow: hidden;
  width: 100%;

  div {
    width: ${props => props.length * 100}%;
  }
`

const Slide = styled.li`
  transform: translate3d(${props => props.index * -100}%, 0, 0);
  transition: transform 250ms;
  float: left;

  figure {
    flex-flow: column nowrap !important;
    align-items: flex-start !important;
    justify-content: flex-start !important;
  }
`

const Slideshow = ({ slides }) => {
  const len = slides.length
  const [index, setIndex] = useState(0)
  const handlePrev = () => {
    setIndex(Math.max(index - 1, 0))
  }
  const handleNext = () => {
    setIndex(Math.min(index + 1, len - 1))
  }
  return (
    <>
      <div className="slideshow-nav">
        <button type="button" onClick={handlePrev} disabled={index === 0}>
          Prev
        </button>
        <span>
          {index + 1} of {len}
        </span>
        <button type="button" onClick={handleNext} disabled={index === len - 1}>
          Next
        </button>
      </div>
      <Slideshower length={len}>
        <div>
          {slides.map(slide => (
            <Slide index={index} key={slide._key}>
              <RichImage image={slide} />
            </Slide>
          ))}
        </div>
      </Slideshower>
    </>
  )
}

export default Slideshow
