import React from "react"

import { graphql } from "gatsby"
import styled from "styled-components"

import Layout from "../components/layout"
import RichImage from "../components/richImage"
import BlockContent from "../components/blockContent"
import SEO from "../components/seo"

export const query = graphql`
  query ProjectQuery($slug: String!) {
    project: sanityProject(slug: { current: { eq: $slug } }) {
      title
      _rawDescription(resolveReferences: { maxDepth: 5 })
      _rawBody(resolveReferences: { maxDepth: 5 })
      mainImage {
        asset {
          id
        }
        alt
      }
      category {
        name
      }
      stack
      live
      code
    }
  }
`

const Wrapper = styled.article`
  display: flex;
  flex-direction: column;
  align-items: center;
`

const figure = 800
const img = 600
const caption = figure - img

const Content = styled.div`
  p {
    max-width: 400px;
    margin: 1em 0;
  }

  h1 {
    margin: 1em 0;
  }
  li {
    padding: 0 2px;
    display: inline;
  }

  figure {
    max-width: ${figure}px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    figcaption {
      max-width: ${caption}px;
      font-size: 0.9em;
    }
  }

  video,
  img {
    display: inline;
    max-height: 80vh;
    max-width: 80%;
    padding-top: 2em;
  }

  video {
    max-height: 500px;
  }
`
const ProjectInfo = styled.section`
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  li {
    display: inline;
    margin-left: 1em;
  }

  img {
    max-height: 600px;
    margin: auto;
  }
`

const ProjectTemplate = ({ data }) => {
  const { project } = data

  const {
    title,
    _rawBody: body,
    mainImage,
    category,
    stack,
    live,
    code,
  } = project
  return (
    <Layout>
      <SEO title={title} />
      <Wrapper>
        <ProjectInfo>
          <div>
            <h1>{title}</h1>
            <RichImage image={mainImage} />
          </div>
          <div>
            {category && category}
            {stack && (
              <ul>
                {stack.map(s => (
                  <li key={s}>{s}</li>
                ))}
              </ul>
            )}
            {code && code}
            {live && live}
          </div>
        </ProjectInfo>
        <Content>
          <BlockContent blocks={body} />
        </Content>
      </Wrapper>
    </Layout>
  )
}

export default ProjectTemplate
